import { Route } from 'react-router-dom';

const makeEmptyStr = (num) => {
    let str = '';
    for (let i = 0; i < num; i++) {
        str += '\xa0';
    }
    return str;
}

const capitalize = (str) => {
    let arr = str.split('');
    arr[0] = arr[0].toUpperCase();
    return str = arr.join('');
}

const arrObjCombiner = (arr1, arr2) => {
    return arr1.map((item, index) => { //arrays must be same len && the indexes must be obj
        return (
            {
                ...item,
                ...arr2[index],
            }
        )
    });
}

const createR = (productIndex, path, clickHand) => {
    console.log(productIndex, path, clickHand);
    // const regex = '(?<=\/catalog\/)[A-Za-z]+';
    // const [routeName] = path.match(regex);
    // const product = productIndex.find((item) => {
    //     return item.ProductType === capitalize(routeName);
    // });
    return productIndex.map((elem) => {
        return (
            <Route key={elem.id} path={`${path}/${elem.id}`} render={() => createComponent(elem, clickHand)} exact />
        );
    });
}

const createComponent = (elem, clickHand) => {
    return (
        <div id={elem.id}>
            <img src={elem.image} alt='' />
            <div>{elem.description}</div>
            <button onClick={() => clickHand()}>Add to Cart</button>
        </div>
    );
}

export { makeEmptyStr, capitalize, arrObjCombiner, createR };